body {
  padding-top: 0 !important;
}

.headermaxW {
  width: 100%;
  max-width: 1240px;
  display: flex;
  margin: 0 auto;
  align-items: center;
  justify-content: center;
}

.logoutBtn {
  background: transparent;
  border: 0;
  font-size: 13px;
  border: 1px solid #fff;
  border-radius: 20px;
  padding: 0 15px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.logoutBtn:hover {
  background-color: #fff;
  color: #f36520;
}

header.header {
  display: flex;
  flex-flow: column;
  background-color: #f36520;
  width: 100%;
}

header.header .topNav {
  width: 100%;
  color: #fff;
  display: flex;
  justify-content: space-between;
}

header.header .topNav .register {
  border: 1px solid #fff;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

header.header .topNav .register:hover {
  background: #fff;
  color: #f36520;
}

header.header .topNav .left,
header.header .topNav .right {
  display: flex;
  padding: 4px 15px;
  align-items: center;
}

header.header .topNav .userinfo {
  display: flex;
}

header.header .topNav .userinfo > div {
  padding: 0 10px;
  text-transform: capitalize;
  display: flex;
  align-items: center;
}

header.header .topNav .userinfo .money {
  padding-left: 0;
}

header.header .topNav .left > div {
  padding: 2px;
  display: flex;
  align-items: center;
}

header.header .topNav .left > div:nth-child(2) {
  padding-left: 20px;
}

header.header .topNav .left > div i {
  margin-right: 3px;
  padding-left: 5px;
}

header.header .topNav .icons {
  display: flex;
}

header.header .topNav .icons .ads {
  position: absolute;
  background: #f36520;
  z-index: 3;
  top: 26px;
  white-space: nowrap;
  display: flex;
  flex-flow: column;
  left: -10px;
  max-height: 0px;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

header.header .topNav .icons .ads.viz {
  max-height: 999px;
}

header.header .topNav .icons .ads > div {
  cursor: pointer;
  padding-bottom: 20px;
  padding: 2px 18px;
}

header.header .topNav .icons > div {
  position: relative;
  width: 30px;
  display: flex;
  align-items: center;
}

header.header .topNav .icons > div i {
  cursor: pointer;
}

header.header .topNav .icons span {
  width: 16px;
  height: 15px;
  border-radius: 20px;
  background-color: #3c4349;
  font-size: 12px;
  position: absolute;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -6px;
  left: 7px;
}

header.header nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fff;
  padding: 10px;
  border-bottom: 2px solid #f36520;
  box-shadow: 1px 18px 20px -22px black;
}

header.header nav .logo {
  margin: 0;
  margin-right: auto;
  height: auto;
}

header.header .navLinks {
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Lato", sans-serif;
}

header.header .navLinks .item {
  text-transform: uppercase;
  margin: 0 20px;
  font-size: 14px;
  font-weight: 400;
  display: flex;
  align-items: center;
  cursor: pointer;
}

header.header .navLinks .item a {
  color: #222;
  -webkit-text-emphasis: none;
          text-emphasis: none;
}

header.header .navLinks .item:last-child {
  background-color: #f36520;
  color: #fff;
  padding: 3px 16px;
  border-radius: 20px;
}

header.header .navLinks .item:last-child a {
  color: #fff;
}

header.header .navLinks .item:last-child:hover {
  color: #fff;
}

header.header .navLinks .item:hover {
  color: #f36520;
}

.mobHeader {
  display: flex;
  flex-flow: column;
  width: 100%;
}

.mobHeader .top {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  padding: 2px 15px;
  background-color: #fff;
  border-bottom: 2px solid #f36520;
}

.mobHeader .top .logo {
  margin-top: 6px;
  position: relative;
  left: -20px;
}

.mobHeader .top button {
  background-color: transparent;
  border: 0;
  color: #3c4349;
  font-size: 20px;
}

.mobHeader .bottom {
  background-color: #fff;
  overflow: hidden;
  max-height: 0px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  display: flex;
  width: 100%;
  flex-flow: column;
  padding: 0 10px;
}

.mobHeader .bottom .icons {
  display: flex;
  margin-top: 20px;
  align-items: center;
  justify-content: space-evenly;
  margin-bottom: 30px;
}

.mobHeader .bottom .icons > div {
  position: relative;
  width: 30px;
  display: flex;
  align-items: center;
}

.mobHeader .bottom .icons > div i {
  cursor: pointer;
}

.mobHeader .bottom .icons span {
  width: 16px;
  height: 15px;
  border-radius: 20px;
  background-color: #f36520;
  color: #fff;
  font-size: 12px;
  position: absolute;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  top: -6px;
  left: 7px;
}

.mobHeader .bottom .navLinks {
  margin-top: 30px;
  text-transform: uppercase;
  text-align: center;
}

.mobHeader .bottom .navLinks .item:nth-child(even) {
  background-color: #eaeaea;
}

.mobHeader .bottom .userinfo {
  display: flex;
  justify-content: space-between;
}

.mobHeader .bottom.opened {
  max-height: 999px;
  box-shadow: inset 3px 20px 20px -23px black;
  padding-top: 30px;
}
