.login {
  margin-top: 15%;
  width: 300px;
  position: fixed;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  margin: auto;
}

.login ::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  text-transform: uppercase;
  color: #32424e;
  font-size: 12px;
}

.login ::-moz-placeholder {
  /* Firefox 19+ */
  text-transform: uppercase;
  color: #32424e;
  font-size: 12px;
}

.login :-ms-input-placeholder {
  /* IE 10+ */
  text-transform: uppercase;
  color: #32424e;
  font-size: 12px;
}

.login :-moz-placeholder {
  /* Firefox 18- */
  text-transform: uppercase;
  color: #32424e;
  font-size: 12px;
}

.login .title {
  text-align: center;
  margin-bottom: 40px;
}

.login .title div {
  font-size: 28px;
  text-transform: uppercase;
  font-size: #32424e;
  font-weight: 100px;
}

.login .title i {
  color: #ed4c14;
  font-size: 38px;
}

.login ul {
  padding-left: 0;
}

.login ul li {
  list-style: none;
  margin: 10px 0px;
}

.login ul li input {
  width: 100%;
  border: 1px solid #c2cad2;
  height: 40px;
  padding-left: 5px;
  border-radius: 3px;
}

.login ul li .loginBtn {
  background: transparent;
  border: 1px solid #ed4c14;
  border-radius: 0;
  width: 100%;
  height: 55px;
  color: #ed4c14;
  text-transform: uppercase;
  font-size: 20px;
  margin-top: 4px;
  -webkit-transition: 0.3s ease-in-out;
  transition: 0.3s ease-in-out;
}

.verifyAcc {
  width: 100%;
  max-width: 400px;
  height: 400px;
  text-align: center;
  text-transform: capitalize;
}

.hasError {
  text-align: center;
  font-size: 15px;
  display: flex;
  flex-flow: column;
}

.hasError > span {
  font-size: 12px;
  margin-top: 4px;
}
